<template>
  <div class="managerManagement">
    <div class="header">
      <div class="query">
        <span class="text">地市局：</span>
        <el-select
          class="input"
          v-model="municipalBureau"
          size="mini"
          clearable
          placeholder="请选择"
          @change="dsjChange"
        >
          <el-option
            v-for="item in municipalBureauList"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <span class="text">县区局：</span>
        <el-select
          class="input"
          v-model="districtBureau"
          size="mini"
          clearable
          placeholder="请选择"
          @change="qxjChange"
        >
          <el-option
            v-for="item in districtBureauList"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <span class="text">供电所：</span>
        <el-select class="input" v-model="powerSubstation" size="mini" clearable placeholder="请选择">
          <el-option
            v-for="item in powerSubstationList"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
      </div>
      <div class="search">
        <span class="text">网格化经理：</span>
        <el-input style="width: 300px" v-model="searchName" size="mini" placeholder="请输入网格化经理名称或账号"></el-input>
        <el-button type="primary" size="mini" @click="getTrafficData">搜索</el-button>
      </div>
    </div>
    <div class="top" v-loading="kkLoading">
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span>用电列表</span>
        </div>
        <div class="chart-main">
          <bdaTable
            class="main-table"
            :tableData="trafficData"
            :headerColor="headerColor"
            :indexWidth="100"
            @clickRow="clickRow"
            :isPage="true"
            :pageConfig="trafficPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </div>

    <!--v-if=false隐藏下面两个模块-- 台区停电地址库。。。-->
    <div class="top-center" v-if="false">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <!-- <span>客户画像委派任务</span> -->
              <span>台区停电地址库</span>
            </div>
            <div class="content-title-right">
              <el-input size="mini" v-model="courtsInfoQueryForm.courtsNameOrIdentification"></el-input>
              <el-button type="primary" size="mini" @click="getCourtsInfoData">查询</el-button>
            </div>
          </div>
          <div class="chart-main">
            <!-- <bdaTable
              class="main-table"
              :tableData="customerAssignmentData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="customerAssignmentPageConfig"
              @changePage="changePage"
            ></bdaTable>-->
            <bdaTable
              class="main-table"
              :tableData="courtsInfoData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="courtsInfoPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>服务质量评价</span>
          </div>
          <div class="chart-main">
            <div class="main-left">
              <div class="main-left-title">本月服务质量排名前五</div>
              <div
                class="main-left-item"
                v-for="(item, index) in evaluationData.slice(0, 5)"
                :key="index"
              >
                <span class="index-block">{{ index + 1 }}</span>
                <div class="index-content">
                  <span class="name">{{ item.name }}</span>
                  <span class="value">{{ item.value }}</span>
                </div>
              </div>
            </div>
            <div class="main-right">
              <div class="main-right-title">本月服务质量排名后五</div>
              <div
                class="main-right-item"
                v-for="(item, index) in evaluationData
                  .slice()
                  .reverse()
                  .slice(0, 5)"
                :key="index"
              >
                <span class="index-block">
                  {{
                  evaluationData.length - index
                  }}
                </span>
                <div class="index-content">
                  <span class="name">{{ item.name }}</span>
                  <span class="value">{{ item.value }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="change-type">
      <span
        class="change-button"
        :class="managerAndCountsActive==='1'?'active':''"
        @click="changeManagerAndCounts('1')"
      >网格化经理</span>
      <span
        class="change-button"
        :class="managerAndCountsActive==='2'?'active':''"
        @click="changeManagerAndCounts('2')"
      >台区</span>
    </div>

