      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>回收率分布</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('recoveryActive','1')"
                :class="recoveryActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('recoveryActive','2')"
                :class="recoveryActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="recoveryRes&&recoveryActive==='1'"
              :barChartsData="recoveryData"
              :barWidth="40"
              :barType="'bar'"
              :yAxiosDw="'%'"
              :name="'回收率分布'"
              :ifPage="true"
              ref="barCharts1"
              @clickBar="getHsParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="recoveryActive==='2'">
              <el-input
                style="width: 300px"
                v-model="recoveryQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getRecoveryDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="recoveryActive==='2'"
              class="content-table"
              :tableData="recoveryDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="recoveryPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>非现金缴费率分布</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('paymentActive','1')"
                :class="paymentActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('paymentActive','2')"
                :class="paymentActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="paymentRes&&paymentActive==='1'"
              :barChartsData="paymentData"
              :barWidth="40"
              :barType="'bar'"
              :yAxiosDw="'%'"
              :ifPage="true"
              ref="barCharts2"
              :name="'非现金缴费率分布'"
              @clickBar="getXjParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="paymentActive==='2'">
              <el-input
                style="width: 300px"
                v-model="paymentQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getPaymentDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="paymentActive==='2'"
              class="content-table"
              :tableData="paymentDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="paymentPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
      </div>
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>客户联络信息有效率分布</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('efficiencyActive','1')"
                :class="efficiencyActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('efficiencyActive','2')"
                :class="efficiencyActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="efficiencyRes&&efficiencyActive==='1'"
              :barChartsData="efficiencyData"
              :barWidth="40"
              :barType="'bar'"
              :yAxiosDw="'%'"
              ref="barCharts3"
              :ifPage="true"
              :name="'客户联络信息有效率分布'"
              @clickBar="getKhParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="efficiencyActive==='2'">
              <el-input
                style="width: 300px"
                v-model="efficiencyQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getEfficiencyDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="efficiencyActive==='2'"
              class="content-table"
              :tableData="efficiencyDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="efficiencyPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>电子渠道覆盖率分布</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('coverageActive','1')"
                :class="coverageActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('coverageActive','2')"
                :class="coverageActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="coverageRes&&coverageActive==='1'"
              :barChartsData="coverageData"
              :barWidth="40"
              :barType="'bar'"
              :yAxiosDw="'%'"
              ref="barCharts4"
              :ifPage="true"
              :name="'电子渠道覆盖率分布'"
              @clickBar="getDzParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="coverageActive==='2'">
              <el-input
                style="width: 300px"
                v-model="coverageQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getCoverageDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="coverageActive==='2'"
              class="content-table"
              :tableData="coverageDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="coveragePageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>抱怨工单分布</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('complainActive','1')"
                :class="complainActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('complainActive','2')"
                :class="complainActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="complainRes&&complainActive==='1'"
              :barChartsData="complainData"
              :barWidth="40"
              :barType="'bar'"
              ref="barCharts5"
              :ifPage="true"
              :name="'抱怨工单分布'"
              @clickBar="getByParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="complainActive==='2'">
              <el-input
                style="width: 300px"
                v-model="complainQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getComplainDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="complainActive==='2'"
              class="content-table"
              :tableData="complainDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="complainPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>停电计划及时通知率</span>
            </div>
            <div class="content-title-right">
              <em
                @click="changeChartAndList('noticeActive','1')"
                :class="noticeActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('noticeActive','2')"
                :class="noticeActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="noticeRes&&noticeActive==='1'"
              :barChartsData="noticeData"
              :barWidth="40"
              :barType="'bar'"
              :yAxiosDw="'%'"
              ref="barCharts6"
              :ifPage="true"
              :name="'停电计划及时通知率'"
              @clickBar="getTdParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="noticeActive==='2'">
              <el-input
                style="width: 300px"
                v-model="noticeQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getNoticeDetailData('rePage')">搜索</el-button>
            </div>
            <bdaTable
              v-if="noticeActive==='2'"
              class="content-table"
              :tableData="noticeDetailData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="noticePageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
      </div>
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>业扩工单超时分布</span>
            </div>
            <div class="content-title-right" v-if="false">
              <em
                @click="changeChartAndList('overTimeActive','1')"
                :class="overTimeActive==='1'?'active':'' "
                class="icon-style iconfont icon-tubiao"
              ></em>
              <em
                @click="changeChartAndList('overTimeActive','2')"
                :class="overTimeActive==='2'?'active':'' "
                class="icon-style iconfont icon-biaoge"
              ></em>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="overTimeRes&&overTimeActive==='1'"
              :barChartsData="overTimeData"
              :barWidth="40"
              ref="barCharts7"
              :ifPage="true"
              :barType="'bar'"
              :name="'业扩工单超时分布'"
              @clickBar="getYkgdParams"
              @setOption="setOption"
            ></barCharts>

            <div class="search" v-if="overTimeActive==='2'">
              <el-input
                style="width: 300px"
                v-model="overTimeQueryForm.name"
                size="mini"
                :placeholder="managerAndCountsText[managerAndCountsActive-1]"
              ></el-input>
              <el-button type="primary" size="mini" @click="getOverTimeDetailData">搜索</el-button>
            </div>
            <template>
              <bdaTable
                v-if="overTimeActive==='2'"
                class="content-table"
                :tableData="overTimeDetailData"
                :headerColor="headerColor"
                :isPage="true"
                :pageConfig="overTimePageConfig"
                @changePage="changePage"
              ></bdaTable>
              <bdaTable
                v-if="detailActive==='1'&&false"
                class="dialog-table"
                :tableData="seatNumebrDetailData"
                :headerColor="'#1899fa'"
                :isPage="true"
                :pageConfig="customerDetails"
                @changePage="changePage"
              ></bdaTable>
            </template>
          </div>
        </div>
        <div class="content-right content-border" v-if="managerAndCountsActive==='1'">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>服务质量评价</span>
            </div>
          </div>
          <div class="chart-main" style="display: flex">
            <div class="main-left">
              <div class="main-left-title">本月服务质量排名前五</div>
              <div
                class="main-left-item"
                v-for="(item, index) in evaluationData.slice(0, 5)"
                :key="index"
              >
                <span class="index-block">{{ index + 1 }}</span>
                <div class="index-content">
                  <span class="name">{{ item.name }}</span>
                  <span class="value">{{ item.value }}</span>
                </div>
              </div>
            </div>
            <div class="main-right">
              <div class="main-right-title">本月服务质量排名后五</div>
              <div
                class="main-right-item"
                v-for="(item, index) in evaluationData
                  .slice()
                  .reverse()
                  .slice(0, 5)"
                :key="index"
              >
                <span class="index-block">
                  {{
                  evaluationData.length - index
                  }}
                </span>
                <div class="index-content">
                  <span class="name">{{ item.name }}</span>
                  <span class="value">{{ item.value }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="showBarTableDialogVisible"
      @closed="handleTcClosed"
      width="80%"
    >
      <div class="barTable" v-loading="barTableLoading">
        <el-table
          :data="barTableList"
          height="250"
          border
          stripe
          :row-class-name="tableRowClassName"
          :cell-class-name="tableCellClassName"
          @cell-click="handleDblclick"
          style="width: 100%"
        >
          <el-table-column width="80" type="index" label="序号"></el-table-column>
          <el-table-column
            v-for="item in tableArray"
            :key="item.valueLabel"
            :prop="item.valueLabel"
            :label="item.name"
          ></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pages.pageNo"
            :page-sizes="[1, 10, 20, 50]"
            :page-size="pages.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pages.total"
          ></el-pagination>
        </div>
      </div>
      <div slot="title">
        <span class="table_title">{{tableTit}}</span>
        <el-form :inline="true">
          <el-form-item :label="`${managerAndCountsActive==='1'?'网格化经理':'台区'}：`">
            <el-input
              v-if="managerAndCountsActive==='1'"
              size="mini"
              v-model="wghjlVal"
              style="width: 200px"
              placeholder="请输入账号或名称"
            ></el-input>
            <el-input
              v-else
              size="mini"
              v-model="tqVal"
              style="width: 200px"
              placeholder="请输入台区编号或者名称"
            ></el-input>
          </el-form-item>
          <el-form-item label="客户编号：">
            <el-input size="mini" v-model="khbh" style="width: 200px" placeholder="请输入客户编号"></el-input>
          </el-form-item>
          <el-form-item v-if="tableTit === '抱怨工单明细'" label="受理时间：">
            <el-date-picker
              class="rangeTimePicker"
              v-model="rangeTime"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              size="mini"
              style="margin: 0px 10px"
              @click="handleSelectDetailed"
            >查询</el-button>
          </el-form-item>
        </el-form>
        <!--                <el-button type="primary" size="mini" @click="exportExcel">导出Excel</el-button>-->
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showBarTableDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="showBarTableDialogVisible = false" size="mini">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="提示" :visible.sync="showYkTableDialogVisible" width="80%">
      <div class="barTable">
        <el-table
          v-loading="ykLoading"
          :data="ykTableList"
          height="250"
          border
          stripe
          :row-class-name="tableRowClassName"
          @cell-click="handleDblclick"
          style="width: 100%"
        >
          <el-table-column width="80" type="index" label="序号"></el-table-column>
          <el-table-column prop="hjmc" label="环节名称"></el-table-column>
          <el-table-column prop="crsj" label="传入时间"></el-table-column>
          <el-table-column prop="tjsj" label="提交时间"></el-table-column>
          <el-table-column prop="cssc" label="超时时长（天）"></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChangeYk"
            @current-change="handleCurrentChangeYk"
            :current-page="ykPages.pageNo"
            :page-sizes="[8, 10, 20, 50]"
            :page-size="ykPages.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="ykPages.total"
          ></el-pagination>
