    //下面这个方法备用
    detailListExportExcel() {
      let path = "";
      let data = {};
      if (this.trafficDetailQueryForm.prop === "khs") {
        path = "/gridManagerPanoramaSupervisor/exportCustomer";
        data = {
          customerType: "0",
          managerAccount: this.trafficDetailQueryForm.managerAccount
        };
      } else if (this.trafficDetailQueryForm.prop === "xhkhs") {
        path = "/gridManagerPanoramaSupervisor/exportCustomer";
        data = {
          customerType: "1",
          managerAccount: this.trafficDetailQueryForm.managerAccount
        };
      } else if (this.trafficDetailQueryForm.prop === "zdgzkhs") {
        path = "/gridManagerPanoramaSupervisor/exportCustomer";
        data = {
          customerType: "2",
          managerAccount: this.trafficDetailQueryForm.managerAccount
        };
      } else if (this.trafficDetailQueryForm.prop === "xls") {
        path = "/gridManagerPanoramaSupervisor/exportLine";
        data = {
          managerAccount: this.trafficDetailQueryForm.managerAccount
        };
      } else if (this.trafficDetailQueryForm.prop === "tqs") {
        path = "/gridManagerPanoramaSupervisor/exportCourts";
        data = {
          managerAccount: this.trafficDetailQueryForm.managerAccount
        };
      } else {
        return;
      }

      axios({
        method: "get",
        url: CP_PATH + path,
        params: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        },
        responseType: "blob"
      })
        .then(res => {
          const content = res.data;
          const blob = new Blob([content]);
          const fileName = "导出信息.xls";
          if ("download" in document.createElement("a")) {
            // 非IE下载
            const elink = document.createElement("a");
            elink.download = fileName;
            elink.style.display = "none";
            elink.href = URL.createObjectURL(blob);
            document.body.appendChild(elink);
            elink.click();
            URL.revokeObjectURL(elink.href); // 释放URL 对象
            document.body.removeChild(elink);
          } else {
            // IE10+下载
            navigator.msSaveBlob(blob, fileName);
          }
        })
        .finally(() => {
          this.$refs.showDetailList.themeLoading = false;
        })
        .catch(() => {
          this.$refs.showDetailList.themeLoading = false;
        });
    },
