    // 回收率分布
    getRecoveryData() {
      this.recoveryRes = false;
      let colorMap = [
        ["#2790FF", "#55BFFF"],
        ["#19A7CD", "#66D6FF"],
        ["#19CDAE", "#77E7BE"],
        ["#29C644", "#8AE671"],
        ["#CC8A00", "#FACE8A"],
        ["#CC4500", "#FA978A"]
      ];
      if (this.managerAndCountsActive === "1") {
        this.managerRecoveryDistributionGraph({
          current: this.barPages.pageNo,
          size: this.barPages.pageSize,
          searchName: this.searchName,
          powerSupplyCode: this.powerSubstation
            ? this.powerSubstation
            : this.districtBureau
            ? this.districtBureau
            : this.municipalBureau
        }).then(res => {
          let dataMap = [];
          let xNameData = [];
          res.records.forEach(e => {
            dataMap.push({
              value: Math.round(e.hsl * 100 * 1000) / 1000,
