              y轴: [{ name: "超时工单数", color: "#808080" }]
            }
          };
          this.overTimeRes = true;
          this.$nextTick(() => {
            this.$refs.barCharts7.pages.total = res.total;
            this.$refs.barCharts7.pages.pageSize = this.barPages.pageSize;
            this.$refs.barCharts7.pages.pageNo = this.barPages.pageNo;
          });
        });
      }
    },
    // 获取业扩工单超时分布详情
    getOverTimeDetailData(data) {
      let columnNameData1 = [
        { prop: "wghjlmc", label: "网格化经理名称" },
        { prop: "wghjlzh", label: "网格化经理账号" },
        { prop: "gds", label: "工单数" }
      ];
      let columnNameData2 = [
        { prop: "tqbs", label: "台区标识" },
        { prop: "tqmc", label: "台区名称" },
        { prop: "gds", label: "工单数" }
      ];
      if (this.managerAndCountsActive === "1") {
        this.managerWorkOrderOvertimeList({
          ...this.overTimeQueryForm,
          managerNameOrNumber: this.overTimeQueryForm.name
        })
          .then(res => {
            this.overTimePageConfig.name = "overTime";
            this.overTimePageConfig.total = res.total;
            this.overTimePageConfig.size = this.overTimeQueryForm.size;
            this.overTimePageConfig.current = this.overTimeQueryForm.current;

            this.overTimeDetailData = {
              tableData: [...res.records],
              columnNameData: columnNameData1
            };
          })
          .catch(err => {
            if (err) {
              this.coverageDetailData = {
                tableData: [],
                columnNameData: columnNameData1
              };
            }
          });
      }
      if (this.managerAndCountsActive === "2") {
        this.courtsWorkOrderOvertimeList({
          ...this.overTimeQueryForm,
          courtsNameOrIdentification: this.overTimeQueryForm.name
        })
          .then(res => {
            this.overTimePageConfig.name = "overTime";
            this.overTimePageConfig.total = res.total;
            this.overTimePageConfig.size = this.overTimeQueryForm.size;
            this.overTimePageConfig.current = this.overTimeQueryForm.current;
            this.overTimeDetailData = {
              tableData: [...res.records],
              columnNameData: columnNameData2
            };
          })
          .catch(err => {
            if (err) {
              this.coverageDetailData = {
                tableData: [],
                columnNameData: columnNameData2
              };
            }
          });
      }
    },
    //获取供电单位编码--地市局
    GetPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "2" //2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.municipalBureauList = res;
      });
    },
    //获取区县局--区县局
    GetQxjPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "3", //2==供电单位==地市局，3==县区局局，4===供电所
        orgCode: this.municipalBureau //上一级编码
      }).then(res => {
        this.districtBureauList = res;
      });
    },
    //获取供电所
    GetGdsPowerAreaCode() {
      this.getPowerAreaCode({
        orgLev: "4", //2==供电单位==地市局，3==县区局局，4===供电所
        orgCode: this.districtBureau //上一级编码
      }).then(res => {
        this.powerSubstationList = res;
      });
    },
    //地市局选择
    dsjChange() {
      this.districtBureau = "";
      this.districtBureauList = [];
      this.powerSubstation = "";
      this.powerSubstationList = [];
      this.GetQxjPowerAreaCode();
    },
    //区县局选择
    qxjChange() {
      this.powerSubstation = "";
      this.powerSubstationList = [];
      this.GetGdsPowerAreaCode();
    }
  }
};
</script>

